<template>
  <Card style="width:auto;margin: 0 auto">
    <p class="report_title">
      舆情报告
    </p>
    <ul>
      <li v-for="item in movieList" style="list-style: none; margin-top: 30px; padding: 10px">
        <div><a :href="item.url" target="_blank" class="title_css">{{ item.title }}</a></div>
        <div class="digest_css">{{item.digest}}</div>
          <div class="source_css">来源:{{item.source}}</div>
          <div class="ReportTime_css">发布时间:{{item.report_time}}</div>
          <div class="clearfloat"></div>
      </li>
    </ul>
  </Card>
</template>

<script>
  import axios from 'axios'
    export default {
      data () {
        return {
          movieList: [],
        }
      },
      methods: {
        response() {
          let that = this;
          let searchword = that.$route.params.word;
          const path = 'http://148.70.148.229:8003/api/report/';
          axios.get(path, {params:{report_title:searchword}}).then(function (response) {
              let msg = response.data.data;
              that.movieList = msg;
            }
          )
        },
      },
      created() {
        this.response()
      },
    }
</script>

<style scoped>
  .title_css {
    font-family: PingFangSC-Medium;
    font-size: 20px;
    font-weight: bold;
    color: #333333;
    letter-spacing: 0;
}
  .digest_css{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: 0;
    line-height: 30px;
  }
  .source_css{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #999999;
    letter-spacing: 0;
    float: left;
  }
  .ReportTime_css {
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #999999;
    letter-spacing: 0;
    float: right;
  }
  .clearfloat{
    clear:both
  }
  .report_title{
    font-family: PingFangSC-Medium;
    font-size: 30px;
    color: #FF3B50;
    text-align: center;
  }
</style>



