import Vue from 'vue'
import Router from 'vue-router'
import Report_info from "../components/Report_info";
import Test from "../components/Test";
import Write_page from "../components/Write_page";
import try_paper from "../components/try_paper";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path:'/index',
      name:'首页',
      component:Test
    },
    {
      path: '/export/:word',
      name: '搜索页面',
      component: Report_info
    },
    {
      path: '/report_list',
      name: '周报列表',
      component: Write_page
    },
    {
      path: '/report/:word',
      name:'周报编辑页',
      component:try_paper
    }



  ]

})



